<template>
  <van-dialog v-model="show" show-cancel-button class="pay" cancelButtonText="已完成支付" confirmButtonText="支付遇到问题，重新支付" cancelButtonColor="#1988FF" title="请确定支付是否完成" confirmButtonColor="#8C8B8B"	@cancel="noShowSheet" @confirm="ConfirmClosing">
  </van-dialog>
</template>

<script>
export default {
  name: "rechargeState",
  data(){
    return{
      show:true
    }
  },
  methods:{
    ConfirmClosing(){
      this.$router.push({
        name:'my'
      })
    },
    noShowSheet(){
      this.$router.push({
        name:'my'
      })
    }
  }
}
</script>

<style scoped lang="scss">
  @import "../../../style/my";
</style>



